.ComicsItem{
    width: 30%;
    margin: 1%;
    align-self: flex-start;
    color: white;
    position: relative;
}
.ComicsItem img{
    width: 100%;
}
.heading{
    font-size: 20px;
    font-weight: bold;
    text-decoration: none;
    color:#070707;
    text-shadow: 0 0 3px white;
}
.ComicsItem p{
    margin-bottom: 5px;
}
.ComicsItem p span{
    color: darkred;
    font-weight: bold;
    text-shadow: 0 0 3px white;
}
.desc{
    font-size: 14px;
    font-style: italic;
    overflow-y: scroll;
    height: 300px;
}
.deleteComicsItem{
    position: absolute;
    top: 5px;
    right: 10px;
    background-color: transparent;
    border: 1px solid red;
    border-radius: 50%;
    color: red;
    font-size: 20px;
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.deleteComicsItem:hover {
    box-shadow: 0 0 3px white;
    text-shadow: 0 0 3px white;
}
